/* Dropdown Container */
.priority-dropdown {
    position: relative;
    display: inline-block;
    cursor: pointer;
    user-select: none;
  }
  
  /* Selected Priority Display */
  .selected-priority {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 5px 10px;
    background-color: #333;
    color: white;
    border-radius: 5px;
    border: 1px solid #555;
    width: 150px;
  }
  
  /* Dropdown Options */
  .priority-options {
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #333;
    border: 1px solid #555;
    border-radius: 5px;
    width: 150px;
    z-index: 1000;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  }
  
  .priority-option {
    display: flex;
    align-items: center;
    gap: 8px;
    padding: 5px 10px;
    color: white;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .priority-option:hover {
    background-color: #444;
  }
  
  /* Priority Circle */
  .priority-circle {
    width: 15px;
    height: 15px;
    border-radius: 50%;
  }
  