/* Form Styling */
form {
    background-color: #000; /* Black background */
    border-radius: 8px;
    padding: 20px;
    border: 1px solid #333; /* Subtle border for form */
  }
  
  /* Task Input */
  input[type="text"] {
    background-color: #333; /* Match dropdown style */
    color: #fff;
    border: 1px solid #555;
    border-radius: 5px;
    padding: 10px;
    width: 100%;
  }
  
  /* Add Task Button */
  button[type="submit"] {
    background-color: #555;
    color: #fff;
    border: 1px solid #777;
    border-radius: 5px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background-color 0.3s;
  }
  
  button[type="submit"]:hover {
    background-color: #777; /* Slightly lighter on hover */
  }
  
  /* Date-Time Picker */
  input[type="datetime-local"] {
    background-color: #333;
    color: #fff;
    border: 1px solid #555;
    border-radius: 5px;
    padding: 10px;
    width: 100%;
  }
  
  /* Priority Dropdown */
  .priority-dropdown {
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .priority-dropdown > .priority-options {
    margin-top: 10px;
  }
  
  /* Form Rows */
  .form-row {
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 10px;
  }
  
  .form-row > div {
    flex: 1; /* Equal spacing between elements */
  }
  